.song__item {
  display: block;
  padding: 10px;
  background: linear-gradient(to bottom right, #ff5e50, #fc5c72, #e34da4, #9a50f8, #4a9ffb, #29caf9);
  filter: grayscale(75%);
  color: #fff;
  text-decoration: none;
}
.song__item-wrapper {
  max-width: 80%;
  margin: 10px auto;
  border-width: 8px;
  border-style: solid;
  filter: grayscale(25%);
  border-image: linear-gradient(to bottom right, #ff5e50, #fc5c72, #e34da4, #9a50f8, #4a9ffb, #29caf9) 1 1;
}
.song__item-wrapper:hover {
  filter: grayscale(0%);
}
.song__item:hover {
  background: linear-gradient(to bottom right, #ff5e50, #fc5c72, #e34da4, #9a50f8, #4a9ffb, #29caf9);
  filter: grayscale(0%);
}
.song__title {
  font-size: 20px;
  font-weight: 900;
}
.song__artist {
  font-size: 20px;
  font-weight: 700;
}
.song__album {
  font-size: 16px;
  font-weight: 500;
}
.accordion__title {
  padding: 0 20px;
  font-size: 20px;
  font-weight: 900;
}
.accordion__content {
  padding: 0 20px;
  overflow: hidden;
  display: none;
}
.accordion__item {
  margin: 10px;
}
